\">\r\n\r\n        </el-table-column>\r\n        <el-table-column label=\"开户人\"\r\n                         prop=\"bank_name\"\r\n                         align=\"center\">\r\n        </el-table-column>\r\n        <el-table-column label=\"银行卡号\"\r\n                         prop=\"account\"\r\n                         align=\"center\">\r\n\r\n        </el-table-column>\r\n\r\n        <el-table-column label=\"提现金额\"\r\n                         prop=\"total_money\"\r\n                         align=\"center\">\r\n\r\n        </el-table-column>\r\n\r\n        <el-table-column label=\"申请时间\"\r\n                         prop=\"create_time\"\r\n                         align=\"center\">\r\n\r\n        </el-table-column>\r\n\r\n      </el-table>\r\n\r\n      <el-dialog title=\"详情\"\r\n                 center\r\n                 :visible.sync=\"detailVisible\">\r\n        <table class=\"table\">\r\n          <tr>\r\n            <th>油品名称</th>\r\n            <th>总后台的油品名称</th>\r\n            <th>油品介绍</th>\r\n            <th>升数</th>\r\n            <th>结算价</th>\r\n            <th>型号</th>\r\n            <th>油品图片</th>\r\n          </tr>\r\n          <tr v-for='(item,index) in detailList'\r\n              :key=\"index\">\r\n            <td>\r\n              {{item.oil_name}}\r\n            </td>\r\n            <td>\r\n              {{item.little_name}}\r\n            </td>\r\n            <td>\r\n              <el-popover placement=\"top-start\"\r\n                          title=\"油品介绍\"\r\n                          width=\"200\"\r\n                          trigger=\"hover\"\r\n                          :content=\"item.about\">\r\n                <el-button slot=\"reference\"\r\n                           type=\"text\"\r\n                           class=\"ellipsis\"\r\n                           style=\"width:100px\">{{item.about}}</el-button>\r\n              </el-popover>\r\n            </td>\r\n            <td>\r\n              {{item.rise}}\r\n            </td>\r\n            <td>\r\n              {{item.price}}\r\n            </td>\r\n            <td>\r\n              {{item.standard}}\r\n            </td>\r\n            <td>\r\n              <img :src=\"item.cover\"\r\n                   ref=\"images\">\r\n            </td>\r\n          </tr>\r\n        </table>\r\n      </el-dialog>\r\n      <div class=\"page_center\"\r\n           v-show=\"page&&page>1\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\nimport Viewer from '@/utils/Viewer'\r\nexport default {\r\n  data () {\r\n    return {\r\n      authList: [],\r\n      threeAuthList: [],\r\n      thCurId: '',\r\n      curId: '',\r\n      list: [],\r\n      seCurId: '',\r\n      token: window.sessionStorage.getItem('bbytoken'), //token令牌\r\n      page: 0,\r\n      pageCount: 0,\r\n      detailList: [],\r\n\r\n      detailVisible: false, //详情弹框状态\r\n      throughLoading: false, //通过加载Loading\r\n      rejectLoading: false, //驳回Loading\r\n    }\r\n  },\r\n\r\n  methods: {\r\n    paging (e) {  //获取供应商列表当前页\r\n      this.page = e\r\n      this.init()\r\n    },\r\n\r\n    async init () {\r\n      try {\r\n        const res = await this.$axios.post('admin/SmCash/smApplyCashList', { token: this.token, page: this.page, status: 2 })\r\n        this.list = res.data.data.list || []\r\n        this.pageCount = res.data.data.rows || 0\r\n\r\n\r\n      } catch (err) {\r\n        throw (err)\r\n      }\r\n    },\r\n    detail () {  //油品详情\r\n      this.$nextTick(() => {\r\n        const ViewerRef = this.$refs.images\r\n        Viewer(ViewerRef)\r\n      })\r\n    },\r\n\r\n\r\n\r\n    // 权限列表,当前所在权限页  \r\n    erAuth () {\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: window.sessionStorage.getItem('bbytoken'),\r\n        id: id\r\n      }).then(res => {\r\n        if (res.data.code == 1) {\r\n          var arr = res.data.data || [];\r\n          for (var i = 0; i < arr.length; i++) {\r\n            if (arr[i].son) {\r\n              if (arr[i].name == '提现') {\r\n                this.seCurId = arr[i].id;\r\n                this.threeAuthList = arr[i].son;\r\n\r\n              }\r\n              for (var j = 0; j < arr[i].son.length; j++) {\r\n                if (arr[i].action != arr[i].son[j].action) {\r\n                  arr[i].action = arr[i].son[0].action;\r\n                }\r\n                if (arr[i].son[j].name == '已驳回' && arr[i].name == '提现') {\r\n                  this.thCurId = arr[i].son[j].id;\r\n                }\r\n              }\r\n            }\r\n          }\r\n          this.authList = arr;\r\n        } else {\r\n          this.$alert(res.data.msg, '提示', {\r\n            type: 'error'\r\n          });\r\n        }\r\n      })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n  created () {\r\n    this.erAuth()\r\n  },\r\n  mounted () {\r\n    this.init() //初始化数据  \r\n  },\r\n}\r\n</script>\r\n<style scoped>\r\n.table img {\r\n  width: 50px;\r\n  height: 50px;\r\n  vertical-align: middle;\r\n  margin: 10px 0;\r\n}\r\n.ellipsis {\r\n  width: 80px !important;\r\n  white-space: nowrap !important;\r\n  text-overflow: ellipsis !important;\r\n  overflow: hidden !important;\r\n}\r\n</style>"],"sourceRoot":""}]);

